import React,{useState} from 'react';
import Container from '@material-ui/core/Container';
import IconButton from '@material-ui/core/IconButton';
import AddCircleIcon from '@material-ui/icons/AddCircle';
import List from './list';



const App =()=>{
  const [inpu, Setinput]=useState();
  const [item, Setitem]=useState([]);
  
  const Addinp=(events)=>{
         Setinput(events.target.value);
                         };

  const Additem=()=>{
    Setitem((oldval)=>{ return[...oldval,inpu]
                      });

    Setinput("");
  }; 
  
  const Delitem=(id)=>{
    
    Setitem((oldval)=>{
      return oldval.filter((arrEem, index)=>{
        return index !== id;
      });
    });
   
  };

  return(<>

  <div className="main__div">
     <Container fixed style={{width:'80vw', overflow:'hidden' }} className="con__div" >
       <br/>
       <h1> ToDo List </h1>
       <br/>
       <input className="inp" value={inpu} type="text" onChange={Addinp} placeholder="Enter Here"   />
       <IconButton onClick={Additem} style={{transform:'translate(0px,0px)'}} className="icnbtn" aria-label="Add">
       <AddCircleIcon style={{ color:'#A1FC99'}} className="addbtn" />
       </IconButton>
       <br/>
       <ol>
         {
          item.map((newElem, index)=>
          {return(
            <List
            text={newElem}
            id={index}
            key={index}
            onSelect={Delitem}
            
            /> 
          )
          }
                  )
         }
       </ol>
       
     </Container>
  </div>
         </>)
};



export default App;