import React from 'react';
import RemoveCircleOutlineIcon from '@material-ui/icons/RemoveCircleOutline';
import IconButton from '@material-ui/core/IconButton';


const List =(props)=>{
    return(
        <>
        <div className="li__div">
        <IconButton onClick={()=>{props.onSelect(props.id)}}  className="icnbtn" aria-label="Remove">
       <RemoveCircleOutlineIcon className="lbtn" /> 
       </IconButton>
       <li> {props.text} </li>
       </div>
        </>
    )
};

export default List;